.dialogs {
	display: grid;
	grid-template-columns: 2fr 10fr;
}
.dialogs_list {
	padding: 20px;
	border-right: 1px solid #000;
}
.dialog_itm {
	border: 1px solid #000;
	cursor: pointer;
	border-radius: 6px;
	margin-bottom: 20px;
	text-align: center;
	padding: 5px 10px;
}
.dialog_itm a {
	text-decoration: none;
}
.dialog_itm.active {
	background: #3D77FC;
}
.dialog_itm:hover {
	background: #FBF9F9;
}
.messages_wrap {
	padding: 40px;
}
.message_itm {
	background: #fff;
	padding: 10px;
	margin-bottom: 10px;
	border-radius: 6px;
}
.new_message_case {
	position: relative;
}
.new_message_case textarea {
	position: relative;
	width: 70%;
	margin-top: 20px;
}
.new_message_case button {
	position: absolute;
	right: 0;
	bottom: 0;
	background: #A7A7A8;
	padding: 5px 10px;
	border-radius: 6px;
	cursor: pointer;
	color: #fff;

}