.case {
	background: #DDDDDD;
	margin-top: 50px;
	padding: 10px 30px;

}

form {
    display: flex;
    align-items: flex-start;
}

.new_txt {
    margin-left: 90px;
	width: 600px;
	height: 100px;
	padding: 10px 10px;
	border: none;
	border-radius: 6px;
	box-shadow: inset 0 0 10px 10px #ECECEC;
}
.case button {
    margin-left: 30px;
	background: #A7A7A8;
	padding: 5px 10px;
	border-radius: 6px;
	cursor: pointer;
	color: #fff;
}