.prof_txt {
    display: grid;
    grid-template-rows: 100px 100px;
    /*grid-template-columns: 1fr 2fr 2fr;*/
    grid-template-areas: "name cont" "stat cont";
    position: relative;
}

.full_name {
    grid-area: name;
    margin-top: 90px;
}

.stat {
    grid-area: stat;
    margin-top: 70px;
    margin-left: 50px;
}
.stat_label {
    opacity: 0.3;
}

.prof_content {
    display: flex;
}

.main_info {
}

.main_info label{
    opacity: 0.3;
}

.contacts {
    grid-area: cont;
    padding-left: 30px;
}
.contacts h3 {
    margin-top: 5px;
    text-decoration: underline;
}
.contacts label {
    color: #9BB2FF;
}
.edit_btn {
    position: absolute;
    top: -2.8rem;
    left: 34rem;
    border: 1px solid #000000;
    border-radius: 4px;
    color: #000000;
    padding: 5px;
    opacity: 0.2;
    transition: 0.5s;
    cursor: pointer;
    font-size: 16px;
}
.edit_btn:hover {
    opacity: 1;
}

.content .prof_icon img {
    width: 200px;
    height: 200px;
    float: left;
    margin-top: 40px;
    border: 4px solid #ffffff;
    border-radius: 10px;
    margin-left: 30px;
    margin-right: 20px;
    position: absolute;
    top: -10rem;
    left: 2.5rem;
    background: aliceblue;
}

.select_img_button {
    position: absolute;
    left: 18rem;
    top: -2.5rem;

}

.select_img_button label {
    border: 1px solid #ffffff;
    border-radius: 4px;
    color: #ffffff;
    padding: 4px;
    opacity: 0.2;
    transition: 0.5s;
    cursor: pointer;
}

.select_img_button label:hover {
    opacity: 1;
}

.content .prof_icon {
    position: relative;
}

.content .prof_icon input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.content .prof_txt h1 {
    margin-left: 50px;
}

.content .prof_txt p {
    margin-bottom: 5px;
    margin-top: 5px;
    margin-left: 30px;
}