.nav {
  grid-area: n;
  background: #C6D6F9;
  padding: 30px 0;
  width: 100%;
}
.nav a {
	text-decoration: none;
	background: #8CAEF9;
	padding: 5px 10px;
	border-radius: 6px;
	cursor: pointer;
	color: #052F8C;
}
.nav a:hover {
	background: #1059F9;
	color: #fff;
}
.nav a.active {
	color: #EAF80D;
}
.nav div {	
	margin-top: 20px;
}