.user_case {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
}
.user_card {
	border: 1px solid #000;
	margin-top: 20px;
	padding: 5px;
	text-align: center;
	min-width: 210px;
	min-height: 180px;
}
.user_icon img {
	width: 100px;
}
.user_txt {
	text-align: center;

}
.user_txt p {
	margin: 5px auto;
}
.follow_btn {
	text-align: center;
	padding: 5px 0;
}
.num_case {
	display: flex;
	flex-wrap: wrap;
    justify-content: center;
}
.num_case span {
	border: 1px solid #000;
	margin: 3px;
	cursor: pointer;
}
.selectPage {
	font-weight: bold;
}